.drop-down {
  overflow: visible;
  height: 50px;
  display: inline-block; }
  .drop-down:hover ul {
    height: 500px;
    -webkit-transition: height 5s;
    transition: height 5s; }
  .drop-down ul {
    overflow: hidden;
    color: rgba(255, 0, 0, 0.5);
    height: 0;
    margin: 0;
    padding: 0;
    -webkit-transition: height 0s;
    transition: height 0s; }
    .drop-down ul li {
      list-style: none; }



